:root {
  --main-color: #00acdc;
}

* {
  box-sizing: border-box;
}

body {
  color: #000;
  margin: 0;
}

html {
  font-family: Roboto, sans-serif;
}

.header {
  background: #fff;
  flex-shrink: 0;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  min-width: 1024px;
  padding: 0 50px;
  display: flex;
}

.link {
  text-transform: uppercase;
  height: 60px;
  margin-right: 20px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 60px;
}

.link:hover {
  color: var(--main-color);
}

.link:last-child {
  margin-right: 0;
}

.logo-link {
  width: 40px;
  height: 40px;
}

.nav__list {
  justify-content: flex-end;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

